body {
  margin: 0;
  background: linear-gradient(135deg, #141e30, #243b55);
  font-family: "Segoe UI", Arial, sans-serif;
  color: #f0f0f0;
}

.container {
  max-width: 800px;
  margin: 50px auto;
  background: #1f1f1f;
  padding: 25px;
  border-radius: 15px;
  box-shadow: 0 8px 25px rgba(0, 0, 0, 0.4);
  backdrop-filter: blur(5px);
}

h1, h2 {
  text-align: center;
  letter-spacing: 0.5px;
}

.form {
  display: flex;
  flex-direction: column;
  gap: 12px;
  background: #292929;
  padding: 20px;
  border-radius: 12px;
  border: 1px solid #333;
}

.form input {
  padding: 12px;
  border: 1px solid #444;
  background: #121212;
  color: #eee;
  border-radius: 8px;
  transition: 0.3s;
}

.form input:focus {
  outline: none;
  border-color: #00aaff;
  box-shadow: 0 0 5px #00aaff;
}

.form button {
  padding: 12px;
  background: #00aaff;
  color: white;
  border: none;
  border-radius: 8px;
  cursor: pointer;
  font-weight: bold;
  transition: 0.3s;
}

.form button:hover {
  background: #0088cc;
  transform: scale(1.03);
}

.list table {
  width: 100%;
  border-collapse: separate;
  border-spacing: 0;
  margin-top: 25px;
  background: #1c1c1c;
  border-radius: 10px;
  overflow: hidden;
}

.list table th {
  background: #00aaff;
  color: white;
  padding: 12px;
  text-transform: uppercase;
  letter-spacing: 1px;
}

.list table td {
  padding: 12px;
  border-bottom: 1px solid #333;
}

.list table tr:hover td {
  background: #2a2a2a;
}

.list button {
  margin: 5px;
  padding: 8px 12px;
  border: none;
  border-radius: 6px;
  cursor: pointer;
  font-size: 14px;
  transition: 0.2s;
}

.list button:first-child {
  background: #ffbb33;
}

.list button:first-child:hover {
  background: #e6a526;
}

.list button:last-child {
  background: #ff4444;
  color: white;
}

.list button:last-child:hover {
  background: #d93636;
}
